@import url('https://fonts.googleapis.com/css2?family=Cairo:wght@200..1000&display=swap');
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family:"Cairo", sans-serif;
}

html{
  direction: rtl;
  height: 150vh;
}

section{
  background: url(../public/images/bg.png);
  background-size: cover;
  min-height: 100vh;
  display: flex;
  align-items: center;
  height: 150vh;
}
section .container{
  width: 650px;
  background: #332a224e;
  padding: 30px 25px;
  backdrop-filter: blur(10px);
  border-radius: 10px;
  border: 1px solid #d7d7d74e;
  margin-right: 10%;
}
section .top_sec{
  display: flex;
  justify-content: space-between;
  padding-bottom: 40px;
  margin-bottom: 40px;
  border-bottom: 1px solid #d7d7d74e;
}
section .top_sec select{
   width: 250px;
   border-radius: 5px;
   outline: none;
   background: #a54f3a;
   padding: 2px 10px;
   color: white;
   font-size: 22px;
   margin-top: 10px;
}
section .top_sec h3{
  font-size: 26px;
  color: white;
}
section .top_sec .date{
  width: 250px;
}
section .top_sec .date{
  font-size: 26px;
  color: white;  
}
.prayer{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 25px;
  background: #c0583e75; 
  margin-bottom: 25px;
  color: #fff;
  font-size: 24px;
  border-radius: 5px;
  border: 1px solid #d7d7d74e;
}